.scaner {
    /*position: fixed;*/
    /*top: 12vh;*/
    /*left: 0;*/
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*background-color: var(--background);*/
    position:relative;
}

#qrCodeContainer {
    position: relative;
    width: var(--ScannerWidth);
    height: var(--ScannerHeight);
    border-radius: 2em;
    overflow: hidden;
    border: 1px solid var(--border);
    margin-bottom: 1em;

}

#qr-shaded-region {
    border-width: 50px !important;
}

video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: auto !important;
    min-width: 100%;
    min-height: 100%;
    transform: translate(-50%, -50%);
}

.qr-message {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50vw;
    margin: 0 auto;
}
.wrapper_message{
    position: absolute;
    top:20%;
}

.start-button {
    position: absolute;
    top: 1.5em;
    right: 1.5em;
    width: 40px;
    height: 40px;
    padding: 0;
    background-color: var(--star-primary);
    border: 0;
    border-radius: 7px;
    font-size: 20px;
    color: #fff;
}

datalist{
    display: flex;
    justify-content: space-between;
    width: 50vw;
}

option{
    padding: 0;
}
input[type="range"]{
    width: 50vw;
    margin: 0;
}

.wrapper{
    position: relative;
}

button{
    border:1px solid var(--border);
    background-color: var(--accent);
    color: var(--star-primary);
    padding: 0.5em;
    font-size: 1em;
    border-radius: 0.5em;
}